          <el-input class="input" maxLength="11" v-model="form.studentGuardianList[0].phone"
                    placeholder="请输入家长联系电话1"></el-input>
        </el-form-item>
        <el-form-item class="item" label="联系电话2">
          <el-input class="input" @input="clearPhone" maxLength="11" v-model="form.studentGuardianList[1].phone"
                    placeholder="请输入家长联系电话2"></el-input>
