        <div class="clear"></div>
        <div @click="other = !other" class="separated-text">
          <span>在校情况</span>
          <i :class="['icon', 'el-icon-caret-top',{'rotate': other}]"></i>
        </div>
        <el-collapse-transition>
          <div v-show="other">
            <el-form-item>
              <div class="textarea-box">
                <p>担任班干部情况</p>
                <el-input class="textarea" type="textarea" :rows="6" v-model="form.performance1"
                          placeholder="请输入担任班干部情况"></el-input>
              </div>
            </el-form-item>
            <el-form-item>
              <div class="textarea-box">
                <p>获得奖励情况</p>
                <el-input :class="[form.attachment? 'small-textarea': 'textarea']" type="textarea" :rows="6"
                          v-model="form.performance2"
                          placeholder="请输入获得奖励情况"></el-input>
                <div v-if="form.attachment" class="cover-img">
                  <img class="img" :src="form.attachment.fileUrl + '?imageMogr2/crop/240x138'"
                       @click="imgDialog = true;bigImgUrl = form.attachment.fileUrl" alt="">
                </div>
                <el-dialog width="1000px" class="img-dialog" :visible.sync="imgDialog">
                  <img :src="bigImgUrl" alt="">
                </el-dialog>
              </div>
            </el-form-item>
            <el-form-item>
              <div class="textarea-box">
                <p>附言</p>
                <el-input class="textarea" type="textarea" :rows="6" v-model="form.remark"
                          placeholder="请输入附言"></el-input>
              </div>
            </el-form-item>
          </div>
        </el-collapse-transition>
        <div class="separated-text">其它情况</div>
        <el-form-item class="item" label="推荐机构">
          <el-select filterable class="input" default-first-option  v-model="form.trainInstitutionId" placeholder="请选择">
            <el-option v-for="item in organizationList"
                       :value="item.id" :key="item.id" :label="item.name"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item class="item" label="培训时间">
          <el-input placeholder="请输入内容" class="input" type="number" v-model="form.institutionDuration">
            <template slot="append">月</template>
          </el-input>
          <el-tooltip content="请输入正整数" placement="top" effect="dark">
            <i class="el-icon-warning"></i>
