        </el-form-item>
      </el-form>
      <div class="clear"></div>
      <div class="btns mb30">
        <!--补录文化生-->
        <div v-if="!isEdit && isCulture" @click="doSubmitC" class="btn-submit mr20">确认</div>
        <!--补录体育生-->
        <div v-if="!isEdit && !isCulture" @click="doSubmitS" class="btn-submit mr20">确认</div>
        <!--考生详情 已报名-->
        <div v-if="isEdit && form.studentStatus === 'APPLIED'" @click="dialogApplied = true;setBatch.batchId = null"
             class="btn-submit mr20">设置考试批次
        </div>
        <div v-if="isEdit && form.studentStatus === 'APPLIED'" @click="setNoPass" class="btn-submit mr20">设置未达标</div>
        <div
            v-if="isEdit && (form.studentStatus === 'NONE_EXAM' || form.studentStatus === 'NONE_QUALIFIED')"
            @click="setBack" class="btn-submit mr20">回退
        </div>
        <div v-if="isEdit && !isCulture" @click="doSubmitS('save')" class="btn-submit mr20">更新考生</div>
        <div v-if="isEdit && isCulture" @click="doSubmitC('save')" class="btn-submit mr20">更新考生</div>
        <div v-if="isEdit" @click="$exportDetail('/export/rStudentInfoExport.do',{id: form.id})" class="btn-submit mr20">
          导出
        </div>
        <div @click="$router.go(-1)" class="btn-clear">返回</div>
      </div>
      <!--设置考试批次-->
      <el-dialog title="设置考试批次" class="dialog" :visible.sync="dialogApplied" width="450px">
        <span class="title">考试批次：</span>
        <el-select v-model="setBatch.batchId" filterable default-first-option class="input" placeholder="请选择">
          <el-option v-for="item in batchList"
                     :value="item.id" :key="item.id" :label="item.title"
          ></el-option>
        </el-select>
        <div class="btns">
          <div @click="setBatchFun" class="btn-submit mr20">确认</div>
          <div @click="dialog = false" class="btn-clear">取消</div>
        </div>
      </el-dialog>
    </main-part>
  </div>

</template>
<script>
  import mixins from '../../../assets/js/mixins'

  export default {
    mixins: [mixins.$exportDetail],
    data() {
      var checkPhone = (rule, value, callback) => {
        if (this.form.studentGuardianList[0].phone && this.form.studentGuardianList[0].phone.length !== 11) {
          callback(new Error('联系电话格式错误'));
        }
        if (this.form.studentGuardianList[1].phone && this.form.studentGuardianList[1].phone.length !== 11) {
          callback(new Error('联系电话格式错误'));
        }
        if (!this.form.studentGuardianList[0].phone && !this.form.studentGuardianList[1].phone) {
          callback(new Error('请至少输入一位家长的联系电话'));
        } else {
          callback();
        }

      }
      var checkHA = (rule, value, callback) => {
        if (this.HA.length === 0) {
          callback(new Error('请选择家庭地址'));
        } else {
          callback();
        }
      }
      var checkSA = (rule, value, callback) => {
        if (this.SA.length === 0) {
          callback(new Error('请选择学校所在区域'));
        } else {
          callback();
        }
      }
      var checkSpec = (rule, value, callback) => {
        if (this.selectedSpeciality.length === 0) {
          callback(new Error('请选择特长类别'));
        } else {
          callback();
        }
      }
      return {
        nav: [{name: "考生管理", path: "go(-1)"}, {name: this.$route.params.id !== '0' ? "查看考生" : "添加考生", path: ""}],
        // 是否为文化生
        isCulture: this.$route.query.type === 'CULTURE',
        // 是否为编辑状态
        isEdit: this.$route.params.id !== '0',
        form: {
          batchId: null,
          classroom: '',
          examCentreId: null,
          seatNum: '',
          name: '',
          sex: null,
          idCard: '',
          schoolName: '',
          address: '',
          specialityRemark: '',
          studentGuardianList: [
            {name: '', salutation: '', phone: '', unit: '', position: ''},
            {name: '', salutation: '', phone: '', unit: '', position: ''}
          ],
          performance1: '',
          performance2: '',
          remark: '',
          trainInstitutionId: null,
          institutionDuration: null
        },
        rules: {
          batchId: {required: true, message: '请选择考试批次', trigger: 'change'},
          examCentreId: {required: true, message: '请选择考点', trigger: 'change'},
          name: {required: true, message: '请输入姓名', trigger: 'blur'},
          phone: {required: true, validator: checkPhone, trigger: 'blur'},
          specialityRemark: {required: true, message: '请输入器乐名称', trigger: 'blur'},
          classroom: {required: true, message: '请输入教室号', trigger: 'blur'},
          seatNum: {required: true, message: '请输入座位号', trigger: 'blur'},
          sex: {required: true, message: '请选择性别', trigger: 'change'},
          idCard: {required: true, message: '请输入身份证号码', trigger: 'blur'},
          schoolName: {required: true, message: '请输入现在就读学校', trigger: 'blur'},
          HA: {required: true, validator: checkHA, trigger: 'blur'},
          SA: {required: true, validator: checkSA, trigger: 'blur'},
          address: {required: true, message: '请输入现在就读学校', trigger: 'blur'},
          selectedSpeciality: {required: true, validator: checkSpec, trigger: 'blur'},
        },
        // 所有的考试批次
        batchList: [],
        // 考点
        examCenterList: [],
        // 家庭所在区划
        HA: [],
        // 学校所在区划
        SA: [],
        // 省市区列表
        options: [],
        // 所有培训机构
        organizationList: [],
        // 报考类别
        examType: [],
        // 特长类别
        specialityList: [],
        // 选中的特长类别
        selectedSpeciality: [],
        // 考试批次弹窗
        dialogApplied: false,
        // 设置考试批次所需参数
        setBatch: {
          batchId: '',
          studentId: this.$route.params.id
        },
        // 在校情况展开收起
        other: false,
        // 奖状放大查看
        imgDialog: false,
        // 奖状图片
        bigImgUrl: ''
      }
    },
    methods: {
      // 获取所有的考试批次
      async getBatchList() {
        this.batchList = await this.$ajax({
          url: '/rbatch/queryBatchList.do',
          query: {
            recruitType: this.$route.query.type,
            keywords: ''
          }
        })
      },
      // 获取当前选中批次下的的考点
      async getExamCenter(id) {
        this.form.examCentreId = null
        this.examCenterList = await this.$ajax({
          url: '/centre/queryExamCentreList.do',
          query: {
            batchId: id || this.$route.params.id,
          }
        })
      },
      // 获取特长生报考类别
      async getExamType() {
        this.examType = await this.$ajax({
          url: '/dict/queryDictList.do',
          query: {
            dictType: 'RECRUIT_SPECIALITY'
          }
        })
      },
      // 获取特长生特长类别
      async getSpeciality() {
        let res = await this.$ajax({
          url: '/dict/queryDictList.do',
          query: {
            dictType: 'SPECIALITY'
          }
        })
        this.specialityList = []
        res.forEach(async (e, i) => {
          this.specialityList.push({
            label: e.name,
            value: e.id,
            children: []
          })

          let res = await this.$ajax({
            url: '/dict/queryDictList.do',
            query: {
              parentId: e.id,
              dictType: e.dictType
            }
          })
          res.forEach(n => {
            this.specialityList[i].children.push({
              label: n.name,
              value: n.id
            })
          })
        })
      },
      // 获取一级地区
      async getRegion() {
        let res = await this.$ajax({
          url: '/region/queryRegionList.do',
        })
        this.options = []
        res.forEach(e => {
          this.options.push({
            label: e.name,
            value: e.id,
            children: []
          })
        })
      },
      // 动态获取二三级数据
      async handleItemChange(val) {
        // 判断是否已经存在子集数据
        for (let i = 0; i < val.length; i++) {
          if (this.HA.indexOf(val[i]) !== -1 || this.SA.indexOf(val[i]) !== -1) {
            return
          }
        }
        // 获取子集数据
        let res = await this.$ajax({
          url: '/region/queryRegionList.do',
          query: {
            parentId: val[val.length - 1]
          }
        })
        let arr = []
        if (val.length === 1) {
          res.forEach(e => {
            arr.push({
              label: e.name,
              value: e.id,
              children: []
            })
          })
          this.options.forEach((e, i) => {
            if (e.value === val[0]) {
              this.options[i].children = arr
            }
          })
        } else if (val.length === 2) {
          res.forEach(e => {
            arr.push({
              label: e.name,
              value: e.id,
            })
          })
          this.options.forEach((e, i) => {
            if (e.value === val[0]) {
              this.options[i].children.forEach((n, m) => {
                if (n.value === val[1]) {
                  this.options[i].children[m].children = arr
                }

              })
            }
          })
        }
      },
      // 获取所有的合作机构
      async getOrganization() {
        this.organizationList = await this.$ajax({url: '/rinstitution/queryTrainInstitutionList.do'})
      },
      // 提交文化生
      async doSubmitC(str) {
        let goIt = async () => {
          let query = this.form
          // 处理家长信息
          for (let j = 0; j < query.studentGuardianList.length; j++) {
            let e = query.studentGuardianList[j]
            let index = false
            // 如果每一项都没填就删除该数组项
            for (let i in e) {
              if (!!e[i]) {
                index = true
                break
              }
            }
            if (!index) {
              query.studentGuardianList.splice(j, 1)
            }
          }
          // 处理学校所在区域
          if (this.SA.length) {
            query.schoolProvinceId = this.SA[0]
            query.schoolCityId = this.SA[1]
            query.schoolDistrictId = this.SA[2]
          }
          // 处理家庭地址
          if (this.HA.length) {
            query.provinceId = this.HA[0]
            query.cityId = this.HA[1]
            query.districtId = this.HA[2]
          }
          // 处理培训时间
          if (this.form.institutionDuration && isNaN(parseInt(this.form.institutionDuration))) {
            this.$message.warning('培训时间请输入正整数')
            return
          }
          if (parseInt(this.form.institutionDuration) < 0) {
            this.$message.warning('培训时间请输入正整数')
            return
          }
          query.institutionDuration = parseInt(this.form.institutionDuration)
          let url = '/rstudent/replenishStudent.do'
          let text = '补录成功'
          if (str === 'save') {
            query.studentId = query.id
            url = '/rstudent/updateStudent.do'
            text = '更新成功'
          }
          await this.$ajax({
            url: url,
            query,
          })
          this.$remind(text)
          this.$router.push({
            path: '/recruit/examinee',
            query: {
              type: this.$route.query.type
            }
          })
        }
        await this.$refs.form.validate((valid) => {
          if (!valid) {
            this.$message.warning('请将必填项填写完整')
            return false
          } else {
            goIt()
          }
        })

      },
      // 特长生提交
      async doSubmitS(str) {
        let goIt = async () => {
          let query = this.form
          // 处理家长信息
          for (let j = 0; j < query.studentGuardianList.length; j++) {
            let e = query.studentGuardianList[j]
            let index = false
            // 如果每一项都没填就删除该数组项
            for (let i in e) {
              if (!!e[i]) {
                index = true
                break
              }
            }
            if (!index) {
              query.studentGuardianList.splice(j, 1)
            }
          }
          // 处理学校所在区域
          if (this.SA.length) {
            query.schoolProvinceId = this.SA[0]
            query.schoolCityId = this.SA[1]
            query.schoolDistrictId = this.SA[2]
          }
          // 处理家庭地址c
          if (this.HA.length) {
            query.provinceId = this.HA[0]
            query.cityId = this.HA[1]
            query.districtId = this.HA[2]
          }
          // 处理身份证号码
          if (this.form.idCard) {
            this.form.idCard = this.form.idCard.toUpperCase()
          }
          // 处理培训时间
          if (this.form.institutionDuration && isNaN(parseInt(this.form.institutionDuration))) {
            this.$message.warning('培训时间请输入正整数')
            return
          }
          if (parseInt(this.form.institutionDuration) < 0) {
            this.$message.warning('培训时间请输入正整数')
            return
          }
          query.institutionDuration = parseInt(this.form.institutionDuration)

          let url = '/rstudent/replenishStudent.do'
          let text = '补录成功'
          if (str === 'save') {
            url = '/rstudent/updateStudent.do'
            text = '更新成功'
            query.studentId = query.id
          } else {
            // 处理特长类别
            query.firstSpecialityTypeId = this.selectedSpeciality[0]
            query.secondSpecialityTypeId = this.selectedSpeciality[1]
          }
          await this.$ajax({
            url,
            query,
          })
          this.$remind(text)
          this.$router.push({
            path: '/recruit/examinee',
            query: {
              type: this.$route.query.type
            }
          })
        }
        await this.$refs.form.validate((valid) => {
          if (!valid) {
            this.$message.warning('请将必填项填写完整')
            return false
          } else {
            goIt()
          }
        })
      },
      // 获取考生详情
      async getDetail() {
        let res = await this.$ajax({
          url: '/rstudent/getStudent.do',
          query: {
            id: this.$route.params.id
          }
        })
        await this.getExamCenter(res.batchId)
        this.form = res
        // 处理家长信息
        if (res.studentGuardianList.length === 1) {
          this.form.studentGuardianList.push({name: '', salutation: '', phone: '', unit: '', position: ''})
        }
        // 处理学校所在区域
        if (res.schoolProvinceId) {
          await this.handleItemChange([res.schoolProvinceId])
          await this.handleItemChange([res.schoolProvinceId, res.schoolCityId])
          this.SA = [res.schoolProvinceId, res.schoolCityId, res.schoolDistrictId]
        }
        // 处理家庭所在区域
        if (res.provinceId) {
          await this.handleItemChange([res.provinceId])
          await this.handleItemChange([res.provinceId, res.cityId])
          this.HA = [res.provinceId, res.cityId, res.districtId]
        }


      },
      // 设置考试批次
      async setBatchFun() {
        if (!this.setBatch.batchId) {
          this.$message.warning('请选择考试批次')
          return
        }
        await this.$ajax({
          url: '/rbatch/distributionBatchStudent.do',
          query: this.setBatch
        })
        this.$remind('考试批次设置成功')
        this.$router.push({
          path: '/recruit/examinee',
          query: {
            type: this.$route.query.type
          }
        })
      },
      // 设置未达标
      async setNoPass() {
        await this.$confirm(`确认将【${this.form.name}】提交的报名信息设为不达标吗？`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
        await this.$ajax({
          url: '/rstudent/noneQualifiedStudent.do',
          query: {
            id: this.$route.params.id
          }
        })
        this.$remind('设为未达标成功')
        this.$router.push({
          path: '/recruit/examinee',
          query: {
            type: this.$route.query.type
          }
        })
      },
      // 设置回退
      async setBack() {
        await this.$ajax({
          url: '/rstudent/unabsorbedStudent.do',
          query: {
            id: this.$route.params.id
          }
        })
        this.$remind('回退成功')
        this.$router.push({
          path: '/recruit/examinee',
          query: {
            type: this.$route.query.type
          }
        })
      },
      // 清除联系电话1的验证
      clearPhone() {
        if (this.form.studentGuardianList[1].phone) {
          this.$refs.form.clearValidate(['phone'])
        } else {
          this.$refs.form.validateField('phone')
        }
      },
      // 更新考生
    },
    watch: {
      'SA': function () {
        if (this.SA.length) {
          this.$refs.form.clearValidate(['SA'])
        }
      },
      'HA': function () {
        if (this.HA.length) {
          this.$refs.form.clearValidate(['HA'])
        }
      },
      selectedSpeciality: function () {
        if (this.selectedSpeciality.length) {
          this.$refs.form.clearValidate(['selectedSpeciality'])
        }
      }
    },
    mounted() {
      this.getOrganization()
      this.getBatchList()
      this.getRegion()
      if (!this.isCulture) {
        this.getExamType()
        this.getSpeciality()
      }
      if (this.isEdit) {
        this.getDetail()
      }
    }
  }
</script>
